	export default {
		data() {
			return {
				questionArr: [],
                selectId: '',
                quesionContent: '',
                qqNumber: '',
                eMail: '',
                showflag: true,
                windowSize: null
			}
		},
		methods: {
            showBtn(){
                this.showflag = true;
            },
            hideBtn(){
                this.showflag = false;
            },
			selectQus(e){
                this.selectId = e.currentTarget.id;
            },
            checkMail(){
                this.showflag = true;
                let mailRegExp = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
                if (this.eMail != '') {
                    if (!mailRegExp.test(this.eMail)) {
                        uni.showToast({
                            title: '请重新输入邮箱',
                            icon: 'none',
                            mask: true
                        });
                        setTimeout(function(){
                            uni.hideToast();
                        },2000)
                        return
                    }
                }
            },
            confirm(){
                if (!this.$store.state.isLogin) {
                    uni.navigateTo({
                        url: '/pages/login/login',
                        animationType: 'slide-in-bottom'
                    });
                    return
                }
                if (this.quesionContent === '') {
                    uni.showToast({
                        title: '详细描述为必填项',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    return false
                }
                uni.request({
                    url: this.websiteUrl + '/api/question/add',
                    method: 'POST',
                    header: {
                        Authorization: this.$store.state.userInfo.loginToken
                    },
                    data: {
                        userId: this.$store.state.userInfo.userId,
                        qids: this.selectId,
                        questionDesc: this.quesionContent,
                        qq: this.qqNumber,
                        email: this.eMail
                    },
                    success: res => {
                        if (res.data.status === 0) {
                            uni.showToast({
                                title: '提交成功',
                                mask: true
                            });
                            setTimeout(function(){
                                uni.hideToast();
                            },2000)
                        } else if (res.data.status === 1001) {
                            uni.showToast({
                                title: res.data.message,
                                icon: 'none',
                                mask: true
                            });
                            setTimeout(function () {
                                uni.hideToast();
                                uni.navigateTo({
                                    url:"/pages/login/login",
                                    animationType:"slide-in-right"
                                });
                            }, 600)
                        } else {
                            uni.showToast({
                                title: res.data.message,
                                icon: 'none',
                                mask: true
                            });
                            setTimeout(function () {
                                uni.hideToast();
                            }, 600)
                        }
                    },
                    fail: () => {},
                    complete: () => {}
                });
            }
		},
        onLoad() {
            const res = uni.getSystemInfoSync();
            this.windowSize = res.windowHeight;
            uni.onWindowResize((res) => {
                if (res.size.windowHeight < this.windowSize) {
                    this.showflag = false;
                } else {
                    this.showflag = true;
                }
            })
            uni.request({
                url: this.websiteUrl + '/api/question/type',
                method: 'GET',
                data: {},
                success: res => {
                    this.questionArr = res.data.questionType;
                },
                fail: () => {},
                complete: () => {}
            });
        }
	}
